import React, { Component } from 'react'
import { Link, Redirect } from 'react-router-dom'
import Error from '../Error/error'

export default class register extends Component {
    constructor(props) {
        super(props)
        this.state = {
            email: '',
            password: '',
            fname: '',
            lname: '',
            phone: '',
            redirect: false,
            error: false
        }
    }

    handleRegister(e) {
        e.preventDefault()
        fetch('http://localhost:9000/signup', {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                email: this.state.email,
                password: this.state.password,
                fname: this.state.fname,
                lname: this.state.lname,
                phone: this.state.phone
            })
        })
        .then( res => res.json())
        .then( res => {
            if(res.message === 'Registration successful') {
                this.setState({redirect: true})
            }
            else {
                alert(res.message)
            }
        })
        .catch( err => {
            this.setState({error: true})
            console.log(err.message)})
    }

    handleEmail(e) {
        this.setState({email: e.target.value})
    }

    handlePassword(e) {
        this.setState({password: e.target.value})
    }

    handleFname(e) {
        this.setState({fname: e.target.value})
    }

    handleLname(e) {
        this.setState({lname: e.target.value})
    }

    handlePhone(e) {
        this.setState({phone: e.target.value})
    }

    render() {
        if(this.state.error) {
            return <Error />
        }
        if(!this.state.redirect) {
            return (
                <div class="post-body">
                    <form action="" id="adminlog">
                        <h1>Sign Up to Post Advert</h1>
                        
                        <input type="text"
                         name="firstname" 
                         placeholder="Enter Firstname"
                         value={this.state.fname}
                         onChange={this.handleFname.bind(this)}
                         /> <br/>
                        <input type="text"
                          name="lastname"
                          placeholder="Enter Lastname"
                          value={this.state.lname}
                          onChange={this.handleLname.bind(this)}
                          /> <br/>
                        <input type="number"
                         name="phonenumber"
                          placeholder="Enter Phone Number"
                          value={this.state.phone}
                         onChange={this.handlePhone.bind(this)}
                          /> <br/>
                        <input type="text"
                         name="email" 
                         type="email" 
                         placeholder="Enter Email"
                         value={this.state.email}
                         onChange={this.handleEmail.bind(this)}
                         /> <br/>
                        <input 
                         name="password"
                         type="password" 
                         placeholder="Password"
                         value={this.state.password}
                         onChange={this.handlePassword.bind(this)}
                         /> <br/>
                        <input type="password" name="password" placeholder="Confirm Password"/> <br/>
                        <button onClick={this.handleRegister.bind(this)}>Sign Up</button>
                        <p>Already have an account? <Link to="/signin" class="signup-link">Login</Link></p>
                    </form>
                </div>
            )
        }
        else {
            return <Redirect to="/signin" />
        }
        
    }
}
