import React, { Component } from 'react'
import { Link, Redirect } from 'react-router-dom'
import Error from '../Error/error'

export default class login extends Component {
    constructor(props) {
        super(props)
        this.state = {
            email: '',
            password: '',
            redirect: false,
            error: false,
            id: ''
        }
    }

    handleLogin(e) {
        e.preventDefault()
        fetch('http://localhost:9000/login', {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                email: this.state.email,
                password: this.state.password
            })
        })
        .then( res => res.json())
        .then( res => {
            if(res.message === 'Login was successful') {
                window.localStorage.setItem('token', res.token)
                window.localStorage.setItem('id', res.id)
                this.setState({id: res.id, redirect: true })
            }
            else {
                alert(res.message)
            }
        })
        .catch( err => {
            this.setState({error: true})
            console.log(err.message)
        })
    }

    handleEmail(e) {
        this.setState({email: e.target.value})
    }

    handlePassword(e) {
        this.setState({password: e.target.value})
    }
    render() {
        if(this.state.error) {
            return <Error />
        }
        if(!this.state.redirect) {
            return (
                <div class="post-body">
                    <form action="" id="adminlog">
                        <h1>Login</h1>
                        <input type="text" 
                        name="email" 
                        type="email" 
                        placeholder="Enter Email"
                        value={this.state.email}
                        onChange={this.handleEmail.bind(this)}
                         /> <br/>
    
                        <input type="password"
                        name="password" 
                        placeholder="Enter Password"
                        value={this.state.password}
                        onChange={this.handlePassword.bind(this)}
                        /> <br/>
                        <button onClick={this.handleLogin.bind(this)}> <i class="fas fa-sign-in-alt"></i> Login</button>
                        <p>Don't have an account? <Link to="/signup" class="signup-link">Signup to begin advert</Link></p>
                    </form>
                </div>
            )
        }
        else {
            return <Redirect to={`/dashboard`} />
        }
        
    }
}
