import React, { Component } from 'react'

export default class changepassword extends Component {
    constructor(props) {
        super(props)
        this.state = {
            password: '',
            redirect: false,
            error: false
        }
    }

   async ChangePassword(e) {
        e.preventDefault()
        const id = await window.localStorage.getItem('id')
        fetch(`http://sellyourmarket.herokuapp.com/api/subsciber/changepassword/${id}`, {
            method: "POST",
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            },
            body: JSON.stringify({
                password: this.state.password
            })
        })
        .then( res => res.json())
        .then( res => {
            alert(res.message)
        })
        .catch( err => {
            this.setState({error: true})
            console.log(err.message)
        })
    }

    handlePassword(e) {
        this.setState({password: e.target.value})
    }
    render() {
        return (
            <div>
                <form action="" id="adminlog">
                        <h1>Change Password</h1>
                        <input type="password"
                        name="password" 
                        placeholder="Enter Password"
                        value={this.state.password}
                        onChange={this.handlePassword.bind(this)}
                        /> <br/>
                        <button onClick={this.ChangePassword.bind(this)}> <i class="fas fa-sign-in-alt"></i>Change Password</button>
                    </form>
            </div>
        )
    }
}
