import React, { Component } from 'react'
import { getSingleSubscriber } from '../../../../api/api'
import { Link } from 'react-router-dom'

export default class account extends Component {
    render() {
        return (
            <div>
                
                <section class="user-display-section">
                    <h2 class="account-overview">Account Overview</h2>
                    <div class="account-info">
                        <div class="account-info-sec">
                            <h3>Account Details  <i class="far fa-edit"></i></h3>
                            <div class="account-details">
                                <p>Name: {this.props.fname} {this.props.lname}</p>
                                <p>Email: {this.props.email}</p>
                                <Link to="/changepassword">CHANGE PASSWORD</Link>
                            </div>
                        </div>
                        <div class="account-info-sec">
                            <h3>Address Book  <i class="far fa-edit"></i></h3>
                            <div class="address-book">
                                <p>Your default advert address: </p> 
                                <article>{this.props.fname} {this.props.lname}</article> 
                                <article>{this.props.address} </article> 
                                <article>{this.props.phone}</article> 
                            </div>
                        </div>
                        <div class="account-info-sec">
                            <h3>Newsletter Preferences</h3>
                            <div class="newsletter-preference">
                                <p>
                                You are currently not subscribed to any of our newsletters.
                                </p>
                                <a href="">EDIT NEWSLETTER PREFERENCES</a>  
                            </div>
                        </div>
                        <div class="account-info-sec">
                            <h3>Subscription Details</h3>
                            <div class="subscription-details">
                                <p>
                                Your currently dont have a subscription plan 
                                </p>
                                <a href="">click here</a> to subscribe

                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
