import React, { Component } from 'react'
import Subscribe from './subscribe'
import Sidebar from '../../sidebar/sidebar'

export default class subMain extends Component {
    render() {
        return (
            <div>
                <div class="user-welcome">
                    <h2>{this.props.user}</h2>
                </div>
                <div class="user-body">
                    <Sidebar />
                    <Subscribe />
                </div>
            </div>
        )
    }
}
