import React, { Component } from 'react'
import Header from '../../layouts/header'
import Footer from '../../layouts/footer'
import ParentSlider from '../slider/parentSlider'
import Ads from '../ads/recent'
import Product from '../categories/others/Others'
import Login from '../Login/login'
import Event from './events/event'
import Video from './video/video'

export default class index extends Component {
    constructor(props) {
        super(props)
        this.state = {
          children: 'children',
          search: '',
          success: false,
          data: [],
          category: '',
          signin: false
        }
      }
      //http://sellyourmarket.herokuapp.com/
      //http://localhost:5000
      async handleSearch(e) {
        e.preventDefault()
        fetch('http://sellyourmarket.herokuapp.com/api/product/category', {
          method: "POST",
          headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({
            category: this.state.search
          })
        })
        .then( res=> res.json())
        .then( res => {
           
           if(res.success === true) {
               
               let cart = res.info[0].category
             this.setState({success: true, data: res.info, category: cart})
           }
          })
        .catch( err => console.log(err.message))
      }
    
      handleChange(e) {
        this.setState({search: e.target.value})
      }
      handleLogin(e) {
        console.log(this.state.signin)
        this.setState({signin: true})
  
      }
    render() {
        if(this.state.success) {
            return (
                <div>
                    <Header
                     onClick={this.handleSearch.bind(this)}
                     value={this.state.search}
                     onChange={this.handleChange.bind(this)}
                     signin={this.handleLogin.bind(this)}
                     />
                      <Product products={this.state.data} category={this.state.category} />
                </div>
               
            )
        }
        else{
            return (
                <div>
                    <Header
                     onClick={this.handleSearch.bind(this)}
                     value={this.state.search}
                     onChange={this.handleChange.bind(this)}
                     signin={this.handleLogin.bind(this)}
                     />
                    <ParentSlider />
                    <Ads />
                    <Event />
                    <Video />
                    <Footer />
                </div>
            ) 
        }
        
    }
}
