import React, { Component } from 'react'
import { Link } from 'react-router-dom'

export default class sidebar extends Component {
    async handleLogOut() {
        await window.localStorage.clear()
    }
    render() {
        return (
            <div>
                <section class="user-menu-section">
                    <div> <Link to='/dashboard'> <i class="far fa-user"></i> My Account </Link> </div>
                    <div> <Link to={`/myadverts`}> <i class="fas fa-location-arrow"></i> My Adverts</Link> </div>
                    <div> <Link to='/placement'> <i class="far fa-address-card"></i> Place Advert</Link> </div>
                    <div> <Link to='/dashboard'> <i class="fas fa-user-clock"></i>   Pending Reviews </Link> </div>
                    <div> <Link to='/subscribe'> <i class="fas fa-newspaper"></i>   Subscribe to a Plan</Link> </div>
                    <div> <Link to='/' onClick={this.handleLogOut.bind(this)}> <i class="fas fa-sign-out-alt"></i>   Logout</Link> </div>
                </section>
            </div>
        )
    }
}
