import React, { Component } from 'react'
import { getSingleSubscriber } from '../../../../api/api'

export default class myadverts extends Component {
    constructor() {
        super()
        this.state = {
            adverts: [],
            hasAdvert: false
        }
    }
    async componentDidMount() {
        let id = await window.localStorage.getItem('id')
        const adverts = await getSingleSubscriber(id)
        if(adverts.product.length > 0) {
            console.log(adverts.product)
            this.setState({adverts: adverts.product, hasAdvert: true })
        }
    }
    render() {
        if(!this.state.hasAdvert) {
            return (
                <div>
                    <section class="user-display-section">
                        <h2 class="account-overview">Adverts Placed</h2>
                        <div class="logo-and-info">
                        <div class="when-no-info">
                            <img src="newslogo.png" alt=""/>
                            <h4>You have placed no advert yet!</h4>
                        </div>
                        <p>
                            All your posted adverts will be saved here for you to access them anytime.
                        </p>
                        <br/>
                        <a href="placement.html" class="placement-button">Place Advert</a>
                        </div>
                    </section>
                </div>
            )
        }
        else {
            return (
                <div>
                    <section class="user-display-section">
                        <h2 class="account-overview">Adverts Placed</h2>
                        <div class="advert-newsletter">
                            {this.state.adverts ? this.state.adverts.map( (advert, index) => {
                               return <div class="adverts" key={index}>
                                <div class="inner-advert">
                                <div class="front-advert">
                                    <img src="assets/images/Screenshot_20180117-201753.png" alt=""/>
                                    <strong>{advert.name}</strong>
                                    <div class="contact-sec">
                                        <p>Whataspp: {advert.phone}</p>
                                        <p>IG_handle: {advert.ig}</p>
                                        <p>Facebook: {advert.fb}</p>
                                        <p>Twitter: {advert.twitter} </p>
                                    </div>    
                                </div>
                                <div class="advert-content">
                                    <h2>About Us</h2>
                                    <address>Location: {advert.address}</address>
                                    <p> {advert.details}</p>
                                    <button class="delete-advert-placed">Delete</button>
                                </div>
                                </div>
                            </div>
                            }) : (
                                <h2>Oops! no adverts yet.</h2>
                            )}
                        </div>
                    </section>
                </div>
            )
        }
    }
}
