import React, { Component } from 'react'
import AccountOverview from './accountOverview/account'
import Sidebar from '../sidebar/sidebar'
import { getSingleSubscriber } from '../../../api/api'

export default class Dasboard extends Component {
    constructor(props) {
        super(props)
        this.state = {
            email: '',
            lname: '',
            fname: '',
            phone: '',
            address: '',
            id: ''
        }
    }

    async componentDidMount() {
        let id = await window.localStorage.getItem('id')
        const user = await getSingleSubscriber(id)
        this.setState({email: user.info.email, lname: user.info.lname, fname: user.info.fname, phone: user.info.phone, id: user.info._id})
    }
    render() {
        return (
            <div>
                <div class="user-welcome">
                    <h2>Welcome, {this.state.fname}</h2>
                </div>
                <div class="user-body">
                    <Sidebar 
                    id={this.state.id}
                    />
                    <AccountOverview 
                    fname={this.state.fname}
                    lname={this.state.lname}
                    email={this.state.email}
                    phone={this.state.phone}
                    address={this.state.address}
                    />
                </div>
            </div>
        )
    }
}
