import React from 'react'

export default function Others(props) {
    return (
        <div>
            <div class="recent-popular">
                <a href="">{props.category}</a> | <a href="">Most Popular</a>
            </div>

                <div class="advert-newsletter">
                    {props.products.map((advs, index) => (
                        <div class="adverts" key={index}>
                        <div class="inner-advert">
                        <div class="front-advert">
                            <img src={"/assets/images/Screenshot_20180117-201753.png"} alt="advimage"/>
                            <strong>{ advs.name}</strong>
                            <div class="contact-sec">
                                <p>Whataspp: {advs.phone}</p>
                                <p>IG_handle: @{advs.ig}</p>
                                <p>Facebook: {advs.fb}</p>
                                <p>Twitter: {advs.twitter} </p>
                            </div>    
                        </div>
                        <div class="advert-content">
                            <h2>About Us</h2>
                            <address>Location: {advs.address}</address>
                            <p>
                                {advs.details}
                            
                            </p>
                        </div>
                        </div>
                    </div>
                    ))}
                    
                </div>
        </div>
    )
}
