import axios from 'axios'

//get all politics news
export async function getAllPoliticsNews (){
    try {
        const politics = await axios.get('/allpolitics')
        return politics.data
    } catch (error) {
        return error.message
    }
}

//get single subscriber 
export async function getSingleSubscriber(id) {
    try {
        const info = await axios.get(`http://localhost:9000/api/subscriber/${id}`)
        return info.data
    } catch (error) {
        return error.message
    }
}

//get all products /api/products
export async function getAllProducts() {
    try {
        const info = await axios.get(`http://localhost:9000/api/products`)
        return info.data
    } catch (error) {
        return error.message
    }
}

//filterByCategory
export async function filterByCategory(category) {
    try {
        const info = await axios.get(`http://localhost:9000/api/product/${category}`)
        return info.data
    } catch (error) {
       return error.message 
    }
}
