import React, { Component } from 'react'
import axios from 'axios'
export default class subscribePath extends Component {
    constructor() {
        super()
        this.state = {
            month: ''
        }
    }
    async handleSubmit(e){
        e.preventDefault()
        let id = await window.localStorage.getItem('id')
        axios.put(`http://sellyourmarket.herokuapp.com/subscribe/${id}`, this.state)
        .then( res => {
            if(res.data.message === 'Your subscription was successful') {
                alert(res.data.message)
                this.props.history.push('/dashboard')
            }
            else {
                alert(res.data.message)
            }
        })
        .catch( err => console.log(err.message))

        
    }
    handleMonth(e) {
        this.setState({month: e.target.value })
    }
    render() {
        return (
            <div>
                <section class="user-display-section">
                        <h2 class="account-overview">Make your subscription here</h2>
                        <div class="post-advert-container">
                            <div class="enter-advert-details">
                                <p>
                                    Select the month: <br/> <br/>
                                    <select value={this.state.month} onChange={this.handleMonth.bind(this)}>
                                        <option>--select month--</option>
                                        <option value="1">1</option>
                                        <option value="2">2</option>
                                        <option value="3">3</option>
                                        <option value="4">4</option>
                                        <option value="5">5</option>
                                        <option value="6">6</option>
                                        <option value="7">7</option>
                                        <option value="8">8</option>
                                        <option value="12">12</option>
                                    </select>
                                </p>
                            </div>
                        </div>
                        <div class="post-advert-button">
                            <button class="post-advert" onClick={this.handleSubmit.bind(this)}><i class="fas fa-upload"></i> Subscribe</button>
                        </div>
                    </section>
            </div>
        )
    }
}
