import React, { Component } from 'react'

export default class subscribe extends Component {
    render() {
        return (
            <div>
                <section class="user-display-section">
                    <h2 class="account-overview">Select a Subscription Plan</h2>
                    <div class="sub-plan">
                        <div class="regular">
                            <h3>Regular Plan</h3>
                            <div class="plan-link">
                                <a href="">
                                    Entitlement: 2 Adverts <br/>
                                    Duration: 1 week (7 days) <br/>
                                    Price: 5000 Naira
                                </a>
                            </div>
                        </div>
                        <div class="premium">
                            <h3>Premiun Plan</h3>
                            <div class="plan-link">
                                <a href="" >
                                    Entitlement: 2 Adverts <br/>
                                    Duration: 2 weeks (14 days) <br/>
                                    Price: 7000 Naira
                                </a>
                            </div>
                        </div>
                        <div class="gold">
                            <h3>Gold Plan</h3>
                            <div class="plan-link">
                                <a href="">
                                    Entitlement: 5 Adverts <br/>
                                    Duration: 1 month (30 days) <br/>
                                    Price: 12000 Naira
                                </a>
                            </div>
                        </div>
                    </div>
                    <div class="border-line"></div>
                    <div class="option-sub">
                        <p class="payment-option2">
                            <strong >If you don't want an online transaction you can do the following</strong>
                        </p>
                        <p class="pricing">
                            pay #2500 for 1 advert that will last for 1 week <br/>
                            pay #3000 for 1 advert that will last for 2 weeks <br/>
                            pay #5000 for 1 advert that will last for 1 month <br/>
                        </p>
                        <p>
                            Pay to any of the below bank details and send your proof of payment and adverts details to either 
                            the email or whatsapp contact below we will confirm and revert in less than 24 hours
                        </p>
                        <div class="bank-detail">
                            <p>
                                Account Name: SellYourMarket.com <br/>
                                Account Number: 2009774643 <br/>
                                Bank Name: Zenith Bank <br/>
                                Account Type: Savings <br/>
                            </p>
                            <h4>OR</h4>
                            <p>
                                Account Name: SellYourMarket.com <br/>
                                Account Number: 3416786581 <br/>
                                Bank Name: Access Bank <br/>
                                Account Type: Savings <br/>
                            </p>
                        </div>
                        <div class="payment-contact">
                            <h4>Send Proof of Payment and Advert Details To:</h4>
                            <p>
                                Email: sellyourmarket@gmail.com <br/>
                                Whatsapp: 09087654312 <br/>
                            </p>
                        </div>
                    </div>
                </section>

                <section class="user-display-section">
                    <h2 class="account-overview">Place an Advert</h2>
                    <div class="logo-and-info">
                    <div class="when-no-info">
                        <img src="newslogo.png" alt=""/>
                        <h4>You are currentlty subscribed to a plan</h4>
                
                    </div>
                
                    <p>
                        Once your subscribed plan has ended you can now subscribe again.
                    </p>
                    </div>
                </section>
            </div>
        )
    }
}
