import React, { Component } from 'react'
import axios from 'axios'

export default class video extends Component {
    constructor() {
        super()
        this.state = {
            videos: []
        }
    }

    async componentDidMount() {
        const videos = await axios.get('http://sellyourmarket.herokuapp.com/api/videos')
        this.setState({ videos: videos.data.info })
    }
    
    render() {
        return (
            <div>
                <div class="video-advert">
                    <h2 class="advert-event">Video Adverts</h2>
                    <div class="video-group" >
                    { this.state.videos ? this.state.videos.map((video, index) => {
                        return (
                            <div class="videos" key={index}>
                                <strong>{video.name}</strong> <br/>
                                <iframe 
                                    src="http://www.youtube.com/embed/XGSy3_Czz8k?autoplay=0autohide=1control=2">
                                </iframe> 
                                <article> 
                                    {video.details}
                                </article>
                            </div>
                        )
                    }) : (
                        <h3>Oops! No advert placed.</h3>
                    )}
                    </div>
                </div>
            </div>
        )
    }
}
