import React, { Component } from 'react'
import { getAllProducts } from '../../api/api'
// import { gql } from 'apollo-boost'
// import { graphql } from 'react-apollo'

// const getAds = gql`
//     {
//         products{
//             name
//             details
//             id
//             phone
//             ig
//             twitter
//             fb
//             address
//         }
//     }
// `
class recents extends Component {
    constructor() {
        super()
        this.state = {
            products: []
        }
    }

    async componentDidMount() {
        const products = await getAllProducts()
        console.log(products)
        this.setState({products: products.info})
    }
    
    render() {
        return (
            <div>
                <div class="recent-popular">
                    <a href="">Most Recent</a> | <a href="">Most Popular</a>
                </div>
                <div class="advert-newsletter">
                    { this.state.products ? this.state.products.map( (advs) => {
                return (
                    <div class="adverts" key={advs.id}>
                        <div class="inner-advert">
                        <div class="front-advert">
                            <img src={"/assets/images/Screenshot_20180117-201753.png"} alt="advimage"/>
                            <strong>{ advs.name}</strong>
                            <div class="contact-sec">
                                <p>Whataspp: {advs.phone}</p>
                                <p>IG_handle: @{advs.ig}</p>
                                <p>Facebook: {advs.fb}</p>
                                <p>Twitter: {advs.twitter} </p>
                            </div>    
                        </div>
                        <div class="advert-content">
                            <h2>About Us</h2>
                            <address>Location: {advs.address}</address>
                            <p>
                                {advs.details}
                            
                            </p>
                        </div>
                        </div>
                    </div>
                )
            }) :(
                <h3>Oops! No advert placed.</h3>
            ) }
                </div>
            </div>
        )
    }
}
export default recents;
// export default graphql(getAds)(recents);
